.full-bg-size-component{
  background-image: url("http://www.employmentpanamacity.com/wp-content/uploads/2018/06/carpenter.jpg");
  background-color: rgb(128,128,128);
  background-blend-mode: multiply;

  height: 600px;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.text-wrap{
  position: absolute;
  left: 0;
  top: 25%;
  bottom: auto;
  width: 100%;
  
  text-align: center;
  font-size:10vw;
  font-family:  sans-serif;
  color: white;
}

hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  color: #556B2F;
  border-top: 1px solid #556B2F;
}

.p{
